import { yupResolver } from '@hookform/resolvers/yup'
import SaveIcon from '@mui/icons-material/Save'
import { LoadingButton } from '@mui/lab'
import { Box, IconButton, Stack, TextField, Typography } from '@mui/material'
import { WebApi } from 'API/CMS/web'
import { useEffect, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import LinkIcon from '@mui/icons-material/Link'
import * as yup from 'yup'
import LocalFileDisplay from 'components/LocalFileDisplay'
import DialogConfirm from 'components/DialogConfirm'
import { IFileLocal } from 'interfaces'
interface FormInput {
	name: string
	ogURL: string
	favicon: string
	ogImage: string
	ogDescription: string
}
export default function Create() {
	const nav = useNavigate()
	const [loading, setLoading] = useState(false)
	const list = useAppSelector((s) => s.CMS.Web.list)
	const [createdId, setCreatedId] = useState(0)
	const [selectOGImg, setSelectOGImg] = useState(false)
	const [favicon, setFavicon] = useState(false)
	const dispatch = useAppDispatch()
	const [ogFile, setOgFile] = useState<IFileLocal>()
	const [faviconFile, setFaviconFile] = useState<IFileLocal>()
	useEffect(() => {
		if (list.find((f) => f.id === createdId)) {
			nav(`/cms/${createdId}/page`)
		}
	}, [createdId, list, nav])
	const schema = yup.object({
		name: yup.string().required(),
		ogURL: yup
			.string()
			.required()
			.matches(/^(http(s)?:\/\/.)(www\.)?[a-z0-9-_/.]{2,256}\.[a-z]{2,6}\b([a-z0-9]*)$/gm),
		ogDescription: yup.string().required(),
	})
	const {
		setValue,
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			name: '',
			ogURL: '',
			favicon: '',
			ogImage: '',
			ogDescription: '-',
		},
	})

	const onSubmit: SubmitHandler<FormInput> = async (body) => {
		try {
			setLoading(true)
			const { id, message, error } = await WebApi.create(body)
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message,
				})
			)
			id && setCreatedId(id)
		} finally {
			setLoading(false)
		}
	}

	return (
		<Stack gap={1} component="form" noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
			{favicon && (
				<DialogConfirm
					open={true}
					title=""
					maxWidth="lg"
					primaryButtonColor="success"
					primaryButtonText="設定"
					disabled={!faviconFile}
					handleClose={() => {
						setFavicon(false)
					}}
					handleSubmit={() => {
						if (!faviconFile) return
						setValue('favicon', faviconFile.name)
						setFavicon(false)
					}}
				>
					<LocalFileDisplay
						handleSelect={(file) => {
							setFaviconFile(file)
						}}
						select={faviconFile}
						onlyUse="img"
						imgSize={100}
						maxHeight={200}
					/>
				</DialogConfirm>
			)}
			{selectOGImg && (
				<DialogConfirm
					open={true}
					title=""
					maxWidth="lg"
					primaryButtonColor="success"
					primaryButtonText="設定"
					disabled={!ogFile}
					handleClose={() => {
						setSelectOGImg(false)
					}}
					handleSubmit={() => {
						if (!ogFile) return
						setValue('ogImage', ogFile.name)
						setSelectOGImg(false)
					}}
				>
					<LocalFileDisplay
						handleSelect={(file) => {
							setOgFile(file)
						}}
						select={ogFile}
						onlyUse="img"
						imgSize={100}
						maxHeight={200}
					/>
				</DialogConfirm>
			)}
			<Stack direction="row" alignItems="center" justifyContent="space-between">
				<Typography
					color="primary.dark"
					sx={{
						fontWeight: 600,
					}}
				>
					ウェブサイト作成
				</Typography>
			</Stack>
			<Stack gap={2} maxWidth="sm">
				<TextField
					variant="outlined"
					size="small"
					margin="dense"
					autoFocus
					required
					label={'ウェブサイト名'}
					placeholder="入力"
					InputLabelProps={{ shrink: true }}
					{...register('name')}
					error={'name' in errors}
				/>

				<TextField
					variant="outlined"
					size="small"
					margin="dense"
					required
					label={'ホームURL'}
					placeholder="例：https://www.example.go.jp"
					{...register('ogURL')}
					error={'ogURL' in errors}
				/>
				<Stack direction="row" gap={1} alignItems="center">
					<TextField
						variant="outlined"
						size="small"
						margin="dense"
						InputProps={{
							readOnly: true,
						}}
						InputLabelProps={{
							shrink: true,
						}}
						sx={{
							flex: 1,
						}}
						required
						label={'Favicon'}
						placeholder="例：https://www.example.go.jp/favicon.ico"
						{...register('favicon')}
						error={'favicon' in errors}
					/>
					<IconButton
						onClick={() => {
							setFavicon(true)
						}}
					>
						<LinkIcon />
					</IconButton>
				</Stack>
				<Stack direction="row" gap={1} alignItems="center">
					<TextField
						variant="outlined"
						size="small"
						margin="dense"
						InputProps={{
							readOnly: true,
						}}
						InputLabelProps={{
							shrink: true,
						}}
						sx={{
							flex: 1,
						}}
						required
						label={'OG画像URL'}
						placeholder="例：https://www.example.go.jp/op-image.png(.jpg,.jpeg,.webp)"
						{...register('ogImage')}
						error={'ogImage' in errors}
					/>
					<IconButton
						onClick={() => {
							setSelectOGImg(true)
						}}
					>
						<LinkIcon />
					</IconButton>
				</Stack>

				<TextField
					variant="outlined"
					size="small"
					multiline
					minRows={3}
					margin="dense"
					label={'ページ説明'}
					placeholder="入力"
					{...register('ogDescription')}
					error={'ogDescription' in errors}
				/>
			</Stack>
			<Box>
				<LoadingButton
					variant="outlined"
					disabled={Object.keys(errors).length > 0}
					loading={loading}
					loadingPosition="start"
					startIcon={<SaveIcon />}
					type="submit"
				>
					作成する
				</LoadingButton>
			</Box>
		</Stack>
	)
}
